@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;900&display=swap");

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: "Inter", sans-serif;
    font-size: 1rem;
    color: #404040;
    text-align: center;
}

/* =================
   Typography 
   ================= */

h1 {
    font-size: 2.25rem;
    font-weight: 300;
    color: #fff;
    margin: 0;
}

h1 + p {
    color: #f18119;
    font-weight: 900;
    font-size: 1.3125rem;
    text-transform: uppercase;
    margin: 0;
}

h2 {
    font-size: 1.3125rem;
    margin: 0;
}

h2 + p {
    color: #f18119;
    font-weight: 900;
    font-size: 1rem;
    margin-top: 0;
    text-transform: uppercase;
}

.top-text {
    font-size: 0.625rem;
    font-weight: 900;
    color: #f18119;
    text-transform: uppercase;
    order: -1;
}

.fine-print {
    font-size: 0.625rem;
}

strong {
    font-weight: 900;
}

/* =================
   Intro section
   ================= */

.intro {
    background-image: url(../images/dark-ribs.jpg);
    background-size: cover;
    background-position: center;
    background-color: #404040;
    padding: 0.5em 2em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 50vh;
}

/* =================
   main-content section 
   ================= */

.main-content {
    padding: 2em 1em;
}

/* =================
   extra modifications
   ================= */

.intro > *,
.main-content > * {
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
}

/* =================
   media queries 
   ================= */

@media (min-width: 500px) {
    body {
        height: 100%;
        font-size: 1.125rem;
        display: flex;
    }

    h1 {
        font-size: 3.5rem;
    }

    h2,
    h1 + p {
        font-size: 1.75rem;
    }

    .intro,
    .main-content {
        width: 50%;
        min-height: 100vh;
    }

    .main-content {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
}
